import React, { Component } from 'react';
import T from 'prop-types';
import { _ } from '@splunk/ui-utils/i18n';
import sortBy from 'lodash/sortBy';
import get from 'lodash/get';
import DashboardContext from '@splunk/dashboard-context';
import Gallery from './Gallery';
import GallerySearch from './GallerySearch';
import GalleryMessage from './GalleryMessage';
import ImageGalleryItem from './ImageGalleryItem';
import GalleryUpload from './GalleryUpload';

class ImageGallery extends Component {
    static contextType = DashboardContext;

    static propTypes = {
        providerType: T.string,
        onClick: T.func,
        enableSearch: T.bool,
    };

    static defaultProps = {
        enableSearch: true,
        onClick: () => {},
    };

    constructor(props) {
        super(props);
        this.state = {
            images: null,
            providerType: null,
            loading: true,
            searching: false,
            error: null,
            warning: null,
        };
    }

    componentDidMount() {
        this.fetchImages();
    }

    getWarning = (images, msg) => {
        if (images && images.length) {
            return null;
        }

        return msg;
    };

    getImageRegistry() {
        const imageRegistry = get(this.context, 'imageRegistry');
        if (imageRegistry == null) {
            throw new Error(_('No image registry found'));
        }
        return imageRegistry;
    }

    handleImageUpload = (files) => {
        const { providerType } = this.state;
        this.setState({ loading: true });
        files.forEach((file) => {
            const fileReader = new FileReader();
            fileReader.onload = async () => {
                const fileName = file.name;
                try {
                    const imageURL = await this.getImageRegistry().upload(
                        fileReader.result,
                        { name: fileName },
                        providerType
                    );
                    const images = [
                        ...this.state.images,
                        {
                            url: imageURL,
                            name: fileName,
                        },
                    ];
                    this.setState({
                        images: sortBy(images, ['name']),
                        warning: null,
                        error: null,
                        loading: false,
                    });
                } catch (err) {
                    this.setState({
                        error: err.message,
                        loading: false,
                    });
                }
            };

            fileReader.readAsDataURL(file);
        });
    };

    /**
     * Handle searching images from all providers
     */
    handleSearch = async (search) => {
        this.setState({ searching: true });
        try {
            const ids = await this.getImagesFromAllProviders({
                search: search.trim(),
            });

            const images = ids.map((idObj) => ({
                url: `${idObj.providerType}://${idObj.id}`,
                name: idObj.filename
                    ? idObj.filename.split('.')[0]
                    : idObj.filename || idObj.metaData.name,
            }));

            this.setState({
                images: sortBy(images, ['name']),
                error: null,
                warning: this.getWarning(images, _('No images found.')),
                searching: false,
            });
        } catch (err) {
            this.setState({
                error: err.message,
                searching: false,
            });
        }
    };

    handleDelete = async (deletedImage) => {
        this.setState({ loading: true });
        try {
            await this.getImageRegistry().deleteByURL(deletedImage.url);
            this.setState((prevState) => {
                const images = prevState.images.filter(
                    (image) => image.url !== deletedImage.url
                );
                return {
                    loading: false,
                    images,
                    warning: this.getWarning(
                        images,
                        _('No images. Please upload images.')
                    ),
                };
            });
        } catch (err) {
            this.setState({
                error: err.message,
                loading: false,
            });
        }
    };

    /**
     * Returns a concatenated array of images from all providers
     * @param {Object} options
     * @returns {Promise<Array>}
     */
    getImagesFromAllProviders = async (options) => {
        const imageRegistry = this.getImageRegistry();
        const providers = imageRegistry.listProviders();
        const imageLists = await Promise.all(
            providers.map(async (provider) => {
                const { type } = provider;
                const ids = await imageRegistry.listIds(type, {
                    ...options,
                    meta: ['name'],
                });
                return ids.map((id) => ({ ...id, providerType: type }));
            })
        );

        return imageLists.reduce(
            (imageList, currentList) => imageList.concat(currentList),
            []
        );
    };

    fetchImages = async () => {
        try {
            let { providerType } = this.props;
            if (!providerType) {
                const provider = this.getImageRegistry()
                    .listProviders()
                    .find((type) => type.isDefault);

                if (!provider) {
                    throw new Error('No image providers found');
                }

                providerType = provider.type;
            }

            const ids = await this.getImagesFromAllProviders();

            const images = ids.map((idObj) => ({
                url: `${idObj.providerType}://${idObj.id}`,
                name: idObj.metaData.name
                    ? idObj.metaData.name.split('.')[0]
                    : undefined,
            }));

            this.setState({
                images: sortBy(images, ['name']),
                providerType,
                error: null,
                loading: false,
                warning: this.getWarning(
                    images,
                    _('No images. Please upload images.')
                ),
            });
        } catch (err) {
            this.setState({
                error: err.message,
                loading: false,
            });
        }
    };

    handleItemClick = (url) => this.props.onClick(url);

    renderSearch() {
        return (
            <GallerySearch
                placeholder={_('Search Images')}
                onSearch={this.handleSearch}
            />
        );
    }

    renderMessage() {
        return (
            <GalleryMessage
                error={this.state.error}
                warning={this.state.warning}
            />
        );
    }

    render() {
        const { enableSearch } = this.props;
        const { images, loading, error, searching } = this.state;

        return (
            <Gallery
                testId="image-gallery"
                isLoading={loading || searching}
                // Do not display search input if it is explicitly disabled, there is an error,
                // or images have not been initialized (null)
                search={
                    enableSearch && !error && images
                        ? this.renderSearch()
                        : null
                }
                message={this.renderMessage()}
                items={images}
                Item={ImageGalleryItem}
                upload={
                    error ? null : (
                        <GalleryUpload
                            fileTypes=".jpeg,.png,.gif"
                            onUpload={this.handleImageUpload}
                        />
                    )
                }
                onItemClick={this.handleItemClick}
                onItemRemove={this.handleDelete}
            />
        );
    }
}

export default ImageGallery;
